    left: 0;
    right: 0;
    top: 0;
    padding: 0.4rem;
    font-size: 0.37rem;
  }
  .center{
    text-align: center;
    margin: 10px 0;
  }
  .a-s{
    display: inline-block;
    width: 2.13rem;
    height: 2.13rem;
    border-radius: 1.07rem;
    overflow: hidden;
  }
  .avator{
    width: 100%;
    height: auto;
  }
  .name{
    font-size: 0.4rem;
    font-weight: bold;
  }
  .skill{
    height: 20%;
    width: 100%;
    display: flex;
    align-items: center;
    padding-left: 0.4rem;
  }
  .icon-skill{
    color: #000;
    font-size: 0.7rem;
  }
  .line{
    height: 0.53rem;
    background: linear-gradient(to right,rgba(0,0,0,0.1),rgba(0,0,0,0.6));
    margin-left: 0.53rem;
    position: relative;
  }
  .line>span{
    line-height: 0.53rem;
    color: #fff;
    font-size: 0.6rem;
    font-weight: bolder;
  }
  .animate-line{
    position: absolute;
    height: 100%;
    background: linear-gradient(to right,rgba(0,0,0,0.1),rgba(0,0,0,0.6));
    animation: grow 2s infinite;
    top: 0;
    left: 0;
  }
  @keyframes grow {
    from{
      width: 0;
    }
    to{
      width: 100%;
    }
  }
  .o-item{
    display: inline-block;
    width: 60%;
    height: 1.33rem;
    background: #333;
    border-radius: 0.13rem;
    margin-top: 0.8rem;
    background-clip: padding-box;
    text-align: center;
    line-height: 1.33rem;
    cursor: pointer;
    color: #fff;
  }
  .o-item .icon{
    font-size: 0.69rem;
  }
  .o-item span{
    vertical-align: top;
    margin-left: 0.27rem;
  }
  .line p{
    font-size: 0.37rem;
    margin-top: 5px;
    font-weight: bold;
  }
  .swiper-container{
    height: 100%;
    width: 100%;
  }
  .swiper-slide h3{
    margin-top: 10px;
    text-align: center;
  }
  .project-card{
    width: 100%;
    height: 100px;
    position: relative;
    top: 0;
    box-sizing: border-box;
    overflow: hidden;
    transition: all 0.3s;
    padding-top:100px;
  }
  .odd{
    background: #f7f7f7;
    z-index: 1;
  }
  .even{
    background: #fff;
    box-shadow: 0px 0px 8px 1px rgba(0,0,0,0.1);
    z-index: 2;
  }
  .nopadding{
    padding: 0;
  }
  .scroll {
    overflow-x: hidden;
    overflow-y: auto;
  }
  .project-box{
    position: absolute;
    left:0px;
    height: 100px;
    opacity: 0;
    width:100%;
    z-index: 9999;
  }
  .project-name{
    text-align: center;
    font-size: 16px;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;

    height: 100px;
    line-height: 100px;
  }
  .icon-quit-project{
    position: absolute;
    left: 10px;
    bottom: 15px;
    font-size: 26px;
    font-weight: bolder;
  }
  .project-content{
    padding: 0 30px;
  }
  .project-content span {
    display: block;
    width: 120px;
    height: 30px;
    border-radius: 16px;
    border: 1px solid #000;
    text-align: center;
    line-height: 30px;
    margin: 0 auto;
    font-size: 16px;
    margin-top: 10px;
  }
  .aboutme li{
