<script>

import $ from 'jquery';
import CanvasTime from '@/util/CanvasTime';
  export default{
    name:'home',
    data:function(){
      return{
        home_height:0,
        home_width:0,
        part_width:0,
        canvas_width:0,
        canvas_heigth:0,
        time:null
      }
    },
    methods:{
      hover(){
        console.log('aaa');
      },
      back:function(index){
        this.initHome();
        if(index == 0){
          this.time.stop();
        }
      },
      bindClick:function(e){
        console.log('ÁÇπÂáª');
        var _this = this;
        $('.part').off('click',_this.bindHover);
        // console.log($(e.target).index());
        var index = $('.part').index(e.currentTarget);
        if(index == 0){
          this.time.start();
        }
        console.log(index);
        for(let i =0;i<$('#home').find('.part').length;i++){
          if(i == index){
            $('#home').find('.part').eq(i).css({
              "left":"0px"
            })
          }
          if(i < index){
            $('#home').find('.part').eq(i).css({
              "left":"-100%"
            })
          }
          if(i > index){
            $('#home').find('.part').eq(i).css({
              "left":"100%"
            })
          }
        }
      },
      initHome:function(){
        var _this = this;
        for(let i =0;i<$('#home').find('.part').length;i++){

          $('#home').find('.part').eq(i).css({
            "left":_this.part_width * i + 'px'
          })
        }
        $('.part').on('click',this,_this.bindClick);
      },
      go:function (path) {
        window.location.href = path;
      }
    },
    created:function(){
      var width = document.body.clientWidth;
      var height = document.body.clientHeight;
      this.home_height = height;
      this.canvas_width = width - width/5;
      this.home_width = width;
      this.part_width = width/5;
      this.canvas_heigth = height/3;
    },

    mounted:function(){
      // ÁªëÂÆö‰∫ã‰ª∂
      var _this = this;
      this.initHome();

      this.time = new CanvasTime({
        el:'#canvas',
        width:_this.home_width,
        height:_this.home_height
      });

    }
  }
</script>
