  text-align: center;
}
.title-p{
  position: absolute;
  left: 0;
  width: 100%;
  top: 50%;
  transform: translateY(-50%);
  cursor: pointer;
  background: #fff;
  background-clip: text;
  -webkit-background-clip: text;
  color: transparent;
}
.title-p:hover{
  background: linear-gradient(rgba(255,255,255,1),rgba(255,255,255,0.3));
  -webkit-background-clip: text;
  color: transparent;
}
.title-p:hover:after{
  background: linear-gradient(rgba(255,255,255,1),rgba(255,255,255,0.3));
}
.title-p:after{
  content: '';
  display: block;
  position: absolute;
  width: 5px;
  height: 100%;
  top: 0;
  right: 30%;
  background: #fff;
}
.title-p span{
  display: block;
}
.icon-back{
  font-size: 100px;
  position: absolute;
  right: 80px;
  cursor: pointer;
  top: 50%;
  transform: translateY(-50%);
  color: #fff;
  z-index: 101;
}
#canvas{
  position: fixed;
  z-index: 100;
  width: 100%;
  left: 0;
  bottom: 0;
}
.content{
  height: 100%;
  color: #fff;
  position: relative;
  z-index: 10;
  padding-top: 10%;
  box-sizing: border-box;
}
.infobox{
  width: 500px;
  height: 300px;
  padding: 15px;
  box-sizing: border-box;
  border: 1px solid #fff;
  color: #fff;
}
.left,.right{
  float: left;
  height: 100%;
  padding-top: 5%;
}
.left{
  width: 30%;
  position: relative;
}
.left::after{
  content: '';
  display: block;
  width: 2px;
  height: 40%;
  background: #fff;
  top: 5%;
  position: absolute;
  right: 0;
}
.right{
  width: 70%;
}
.avator{
  width: 180px;
  height: 180px;
  display: inline-block;
  text-align: center;
}
.avator p{
  font-size: 18px;
  color: #fff;
}
.avator_img{
  width: 140px;
  height: 140px;
  display: inline-block;
  border-radius: 70px;
  background: url("../assets/IMG_0606.jpg") no-repeat;
  background-size: 100% auto;
}
.base-info{
  height: 20%;
}
.base-info p{
  display: inline-block;
  float: left;
  line-height: 84px;
  padding-left: 15px;
  font-size: 20px;
  color: #fff;
}
.base-info p>span{
  margin-right: 30px;
}
.card-box{
  height: 70%;
}
.card-box > div{
  width: 33.3333%;
  float: left;
  height: 100%;
  box-sizing: border-box;
  padding: 0 2px;
  transform: translateZ(0) !important;
}
.card-info{
  border: 1px solid #fff;
  height: 100%;
  overflow: hidden;
  position: relative;
  box-sizing: border-box;
  padding-top: 45%;
  padding-left: 20%;
}
.card-info>h2{
  font-size: 28px;
  color: #fff;
  position: relative;
}
.card-info>h2:before{
  content: '';
  height: 50%;
  display: block;
  width: 3px;
  position: absolute;
  left: -10px;
  top: 7px;
  background: #fff;
}
.card-info>p{
  font-size: 16px;
  color: #f1f1f1;
}
.card-info-white-card{
  width: 60px;
  height: 60px;
  border-radius: 30px;
  background: #fff;
  position: absolute;
  top: -60px;
  right: -60px;
  transition: all 0.3s;
}
.card-info:hover .card-info-white-card{
  transform: scale(20) translateZ(0);
}
.center{
  text-align: center;
  font-size: 16px;
  color: #fff;
}
.card-info:hover .card-info-wenzi{
  top: 0;
}
.card-info-wenzi{
  position: absolute;
  overflow: auto;
  width: 100%;
  left: 0;
  padding: 10px;
  box-sizing: border-box;
  z-index: 10;
  height: 100%;
  background: #fff;
  top: 164%;
  transition: all 0.3s;
}
.card-info-wenzi::-webkit-scrollbar {display:none}

.card-info-wenzi>p{
  color: #333;

}
.card-info-wenzi>h3{
  font-size: 18px;
  margin-top: 10px;
}
.content1{
  width: 60%;
  height: 80%;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%);
}
.skill{
  height: 20%;
  width: 100%;
  display: flex;
  align-items: center;
}
.icon-skill{
  color: #fff;
  font-size: 40px;
}
.line{
  height: 30px ;
  background: linear-gradient(to right,rgba(255,255,255,0.1),rgba(255,255,255,0.8));
  margin-left: 30px;
  position: relative;
}
.line>span{
  line-height: 30px;
  color: #fff;
  font-size: 32px;
  font-weight: bolder;
  display: inherit;
}
.animate-line{
  position: absolute;
  height: 100%;
  background: linear-gradient(to right,rgba(255,255,255,0.1),rgba(255,255,255,0.8));
  animation: grow 2s infinite;
  top: 0;
  left: 0;
}
@keyframes grow {
  from{
    width: 0;
  }
  to{
    width: 100%;
  }
}
.content2{
  overflow: hidden;
}
.content2 .project-box{
  width: 33.33%;
  height: 50%;
  position: relative;
  float: left;
  /*ÈÄèÊòéËæπÊ°Ü*/
  border: 5px solid hsla(0,0%,90%,0);
  background-clip: padding-box;
  box-sizing: border-box;
  cursor: pointer;
  overflow: hidden;
}
.content2 .project-box:hover img{
  height: 140%;
}
.content2 .project-box img{
  position: absolute;
  width: auto;
  height: 100%;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  z-index: -1;
  transition: all 0.3s;
}
.pro-mengban{
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background: rgba(0,0,0,0.7);
  z-index: 1;
}
.border{
  position: absolute;
  z-index: 2;
  background: #fff;
  transition-property: all;
  transition-duration:0.15s;
}
.border-t{
  height: 2px;
  width: 0%;
  top: 0;
  left: 0;
}
.border-r{
  height: 0%;
  width: 2px;
  top: 0;
  right: 0;
  transition-delay: 0.15s;
}
.border-b{
  height: 2px;
  width: 100%;
  bottom: 0;
  left: 100%;
  transition-delay: 0.3s;
}
.border-l{
  height: 100%;
  width: 2px;
  top: 100%;
  left: 0;
  transition-delay: 0.45s;
}
.content2 .project-box:hover .border{
  display: block;
}
.content2 .project-box:hover .border-t{
  width: 100%;
}
.content2 .project-box:hover .border-r{
  height: 100%;
}
.content2 .project-box:hover .border-b{
  left: 0;
}
.content2 .project-box:hover .border-l{
  top: 0;
}
.pro-wenzi{
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  z-index: 3;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 30px;
  box-sizing: border-box;
  text-align: center;
}
.pro-wenzi h1{
  font-size: 20px;
}
.pro-wenzi p{
  font-size: 16px;
}
.card-info-wenzi p{
  font-size: 16px;
}
.o-item{
  width: 25%;
  height: 60px;
  background: #fff;
  float: left;
  border: 5px solid hsla(0,0%,0%,0);
  box-sizing: border-box;
  margin-top: 30px;
  background-clip: padding-box;
  text-align: center;
  line-height: 50px;
  cursor: pointer;
}
.o-item .icon{
  font-size: 30px;
  vertical-align: top;
}
.o-item span{
  vertical-align: top;
  margin-left: 10px;
  font-size: 16px;
}
.moto{
  width: 100%;
  position: fixed;
  z-index:2;
  color: #fff;
  font-size: 20px;
  left: 0;
  bottom: 33.33%;
  text-align: center;
}
.aboutme{
  font-size: 16px;
}

</style>



